export default {
	data() {
		return {
			money:'',
			token:'',//onShow时获取token存起来，以便每次发送请求都要重新获取
			rebateMoney:'',
		}
	},
	onShow(){
		this.token = uni.getStorageSync('token')
		uni.request({
			url: this.url + '/mobile/realData',
			method:'GET',
			header: {
				'content-type': 'application/x-www-form-urlencoded' ,// 默认值
				'token': this.token
			},
			data:{
				
			},
			success: (res) => {
				console.log(res);
				this.rebateMoney = res.data.rebateMoney+''
			}
		})
	},
	methods: {
		commit(){
			let _this = this
			if(this.money == ''){
				uni.showModal({
					title: '提现',
					content:'请输入提现金额'
				})
				return false
			}else if(this.money <= 0){
				uni.showModal({
					title: '提现',
					content:'提现金额需大于0'
				})
				return false
			}
			let reg = /^[0-9]{1,9}\.{0,1}\d{0,1}$/
			if(!reg.test(this.money)){
				uni.showModal({
					title: '提现',
					content:'提现金额最多保留一位小数'
				})
				return false
			}else{
				uni.request({
					url: this.url + '/mobile/withdrawDeposit',
					method:'GET',
					header: {
						'content-type': 'application/x-www-form-urlencoded' ,// 默认值
						'token': this.token
					},
					data:{
						amount:this.money
					},
					success: (res) => {
						console.log(res);
						if(res.data.result){
							uni.showToast({
								title: '提现成功',
								duration: 2000,
								success:function(){
									_this.money = ''
									uni.request({
										url: _this.url + '/mobile/realData',
										method:'GET',
										header: {
											'content-type': 'application/x-www-form-urlencoded' ,// 默认值
											'token': _this.token
										},
										data:{
											
										},
										success: (res) => {
											console.log(res);
											_this.rebateMoney = res.data.rebateMoney+''
										}
									})
								}
							});
						}else{
							uni.showToast({
								title: res.data.msg,
								duration: 2000,
								icon:'none'
							});
						}
					}
				})
			}
		}
	}
}
